<template>
  <transition name="confirm-fade">
    <div class="confirm-main" v-show="showFlag" @click.stop>
      <div class="confirm-wrapper">
        <div class="confirm-content">
          <div class="title-text">
            <p class="title">
              {{ title }}
            </p>
            <p class="text">
              {{ text }}
            </p>
          </div>
          <slot></slot>
          <div class="operate">
            <div @click="cancel" class="operate-btn">
              {{ stopBtnText }}
            </div>
            <div @click="cancel" class="operate-btn">
              {{ cancelBtnText }}
            </div>
            <div @click="confirm" class="operate-btn">
              {{ confirmBtnText }}
            </div>
          </div>
        </div>
      </div>
    </div>
  </transition>
</template>
<script>
export default {
  name: 'cConfirm',
  props: {
    title: {
      type: String,
      default: '提示信息'
    },
    text: {
      type: String,
      default: '您确定要下班吗！'
    },
    confirmBtnText: {
      type: String,
      default: '确定'
    },
    cancelBtnText: {
      type: String,
      default: '取消'
    },
    stopBtnText: {
      type: String,
      default: '去办理'
    }
  },
  data() {
    return {
      showFlag: false
    }
  },
  methods: {
    show() {
      this.showFlag = true
    },
    hide() {
      this.showFlag = false
    },
    cancel() {
      this.hide()
      this.$emit('cancel')
    },
    //确定
    confirm() {
      this.hide()
      this.$emit('confirm')
    }
  }
}
</script>
<style lang="scss" scoped>
  .confirm-main{
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 1000;
    background: rgba(0,0,0,.3);
    &.confirm-fade-enter-active {
      animation: confirm-fadein 0.3s;
      .confirm-content {
        animation: confirm-zoom 0.3s
      }
    }
    .confirm-wrapper{
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%,-50%);
      z-index: 1001;
      border-radius: 10px;
      .confirm-content {
        background: #fff;
        width: 300px;
        border-radius: 10px;
        .title-text {
          padding-bottom: 10px;
        }
        .title{
          padding: 19px 15px 2px ;
          line-height: 22px;
          text-align: center;
          font-size: 18px;
          color: #000000;
        }
        .text {
          text-align: center;
          font-size: 14px;
          margin: 20px 0;
        }
        .operate{
          display: flex;
          align-items: center;
          text-align: center;
          font-size: 16px;
          flex-direction: column;
          .operate-btn{
            width: 100%;
            height: 50px;
            line-height: 50px;
            color: #f37836;
            border-top: 1px solid $gray;
          }
        }
      }
    }
  }
  @keyframes confirm-fadein{
    0%{
      opacity: 0
    }
    100% {
      opacity: 1
    }
  }
  @keyframes confirm-zoom {
    0% {
      transform: scale(0)
    }
    50% {
      transform: scale(1.1)
    }
    100% {
      transform: scale(1)
    }
  }
</style>

